.container_header {
  max-width: 768px;
  overflow: hidden;
  margin: 0 auto;
  padding: 0;
}



.header_color1 {
  margin: 0 auto;
  padding: 0;
  max-width: 768px;
  background: #663F04;
  overflow: hidden;
  display: flex;
  justify-content: center;
  background-image: url('../../images/Ellipse.png');
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /* background:
    radial-gradient(ellipse, rgb(173, 107, 7) 0%, rgb(143, 88, 6) 0%, rgb(102, 63, 4) 100%); */

}


.borders {
  max-width: 100%;
  padding: 21px;
  padding-bottom: 0;
  /* position: absolute; */
  position: relative;
  top: -9px;
  padding-bottom: 100px;
  /* overflow: hidden; */
}

.text_little_header {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 5px;
  line-height: 6px;
  letter-spacing: 0.3em;
  margin: 0 auto;
  color: #FFFFFF;
  width: 248px;
  height: 12px;
  margin-bottom: 10px;
}

.name_user {
  width: 110px;
  height: 34px;
  width: 110px;
  font-family: Vollkorn;
  font-size: 24px;
  line-height: 33px;
  color: #FFFFFF;


  font-family: Vollkorn;
  font-size: 24px;
  line-height: 33px;
  color: #FFFFFF;
}


.text_prise {
  width: 298px;
  height: 54px;
  font-family: Vollkorn;
  font-size: 24px;
  line-height: 27px;
  display: block;
  color: #FFFFFF;
  position: relative;
  z-index: 1;
}

.text_prise::after {
  content: '';
  margin: 0;
  position: absolute;
  width: 239px;
  height: 10px;
  left: 0px;
  top: 58px;
  z-index: 11;
  border: 5px solid #DBAB00;
}

.header_text_description {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  width: 176px;
  height: 148px;
  color: #FFFFFF;
}

.header_button {
  text-decoration: none;
}

.header_button_text {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-decoration: none;
  width: 183px;
  height: 55px;
  padding: 13px 16px 0px 17px;
  /* background-color: red; */
  background: #A70B0B;
  box-shadow: 0px 0px 15px #DD9E23;
  white-space: pre-line;
}

.header_woman {
  right: 10px;
  bottom: -19px;
  width: 77%;
  margin-left: auto;
  margin-right: -6em;
  position: absolute;

}

.block_signature {
  font-family: Roboto;
  font-style: italic;
  font-weight: normal;
  font-size: 7px;
  line-height: 7px;
  white-space: pre-line;
  color: #FFFFFF;
  position: absolute;
  bottom: -9px;
  right: -5px;
  margin: 0;
  width: 186px;
  height: 39px;
  padding: 12px 18px 0px 51px;
  background: #000000;
  opacity: 0.5;
}

.block_signature::before {
  content: url('../../images/filimonova-podpis.png');
  margin: 0;
  display: block;
  width: 30px;
  height: 20px;
  position: absolute;
  top: -14px;
  left: -2px;
}

@media (min-width: 768px) {
  .header_color1 {
    margin: 0 auto;
    padding: 0;
    min-width: 768px;
    background: #663F04;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background-image: url(/static/media/Ellipse.e7799fe7.png);
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .borders {
    max-width: 100%;
    padding: 48px;
    padding-bottom: 0;
    position: none;
    padding-bottom: 0;
    overflow: visible;
  }

  .header_woman {
    right: -90px;
    bottom: -19px;
    width: 101%;
    margin-left: auto;
    margin-right: -17em;
    position: absolute;
  }

  .header_text_description {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    width: 276px;
    /* height: 148px; */
    color: #FFFFFF;
  }

  .block_signature {
    font-family: Roboto;
    font-style: italic;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    white-space: pre-line;
    color: #FFFFFF;
    position: absolute;
    bottom: -10px;
    right: -378px;
    margin: 0;
    width: 397px;
    height: 91px;
    padding: 17px 35px 0px 11px;
    background: #000000;
    opacity: 0.5;
  }

  .block_signature::before {
    content: url('../../images/filimonova-podpis333.png');
    margin: 0;
    display: block;
    width: 74px;
    height: 40px;
    position: absolute;
    top: -53px;
    left: 202px;
  }
}
